<template>
  <div>
    <h3>Products</h3>

    <v-row>
      <v-col
        sm="6"
        md="4"
        v-for="filteredProduct in filteredProducts"
        :key="filteredProduct.name"
      >
        <VerticalProduct :product="filteredProduct" :addToCart="addToCart" />
      </v-col>
    </v-row>
  </div>
</template>

<script>
import VerticalProduct from "../products/VerticalProduct";

export default {
  components: {
    VerticalProduct,
  },
  methods: {
    addToCart(item, quantity = 1) {
      this.$store.commit("addItemToCart", { item, quantity });
      this.$store.commit("updateSnackbar", { show: true });
    },
  },
  data() {
    return {};
  },
  computed: {
    priceFilter: {
      get() {
        return this.$store.getters.priceFilter;
      },
    },
    filteredProducts() {
      return this.$store.state.products.filter((product) => {
        switch (this.priceFilter) {
          case "Any":
            // console.log("any");
            return product;
          case "Under $5000":
            // console.log("Under $5000");
            return product.price < 5000;
          case "$5000 to $10000":
            // console.log("$5000 to $10000");
            return product.price >= 5000 && product.price < 10000;
          case "$10000 to $15000":
            // console.log("$10000 to $15000");
            return product.price >= 10000 && product.price <= 15000;
          case "Over $15000":
            // console.log("Over $15000");
            return product.price >= 15000;
        }
      });
    },
  },
};
</script>